import React from 'react'

let projectText = {
    a: 'This timeline of literary periods provides a neatly visualized UI through which to learn more about famous writers. However, my intention with this project was much more focused on data. When you look closer at the code, you will see that the entries are collected from a vast array of raw data – a timeline of about 400 periods, authors and works. The project is therefore an exercise in extracting and organizing unruly data into something intuitive and visually pleasing.',
    b: `This sweet and simple app was made purely with the intention of entertaining my one-year-old daughter, Clara and also training her ear to differentiate between notes. Research shows that if you pair pitch with meaning early in a child's life, it is possible to help the child develop perfect pitch. To this end, Animal Sounds pairs each note to a picture of an animal, which pops up on the screen when a note button is pushed`,
    c: 'I am in the process of building the website and the portfolio will be updated soon.',
    d: 'This visualization is inspired by Wes Bos and uses flexbox and simple transitions to achieve a highly responsive and and intuitive open/close UI.',
    e: 'I am in the process of building the website and the portfolio will be updated soon.',
    f: 'I am in the process of building the website and the portfolio will be updated soon.',
    g: 'I am in the process of building the website and the portfolio will be updated soon.',
}


export const TileInfo = ({projectTitle, tileText, gridLetter}) => {
  
    function handleDescription() {
        var filtered = {}
        for(var key in projectText) { 
          if(key[0] === gridLetter){
            filtered[key] = projectText[key]
          }
        }
        let text = Object.values(filtered)
    return text
    
    }
    
        return (
            <div className='tileInfo'>
                <h2 className='projectTitle'>{projectTitle}</h2>
                <p className={tileText ? 'tileText' : 'hidden'}>{handleDescription()}</p>
               
            </div>
        )
}

export default TileInfo
